.allImagesContent {
    display: flex;
    flex-direction: row;
    width: 600px;
    margin-top: 10px;
    margin-bottom: 0;
    overflow: hidden;
    height: 400px;
    text-align: left;
    position: relative;
}
#sliderImg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    transition: all 0.5s linear ;
}
#sliderImg img {
    width: 100%;
    height: 100%;
    
}

#sliderImg.activeSlider {
    opacity: 1;
    transform: translateX(0);
}

#sliderImg.lastSlider {
    transform: translateX(-100%);
}
#sliderImg.nextSlider {
    transform: translateX(100%);
}
.btnSliders{
    display: flex;
    flex-direction: row;
    justify-content: center;
    text-align: left;
    margin-bottom: 10px;
    
}
.btnSlider{
    background-color: transparent;
    color: #756d66;
    border: none;
    margin-left: 5px;
}
.btnSlider:hover{
    color: red;
    cursor: pointer;
    
}