* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.navSection {
    display: flex;
    flex-direction: row;
    justify-content: center;
    background-color: rgba(0, 0, 0, 0.2);
    margin-top: 10px;
    padding: 5px;
}
.navSection a {
    text-decoration: none;
    font-family: Arial, Helvetica, sans-serif;
    margin-right: 10px;
    padding: 2px 5px;
    border-bottom: 2px solid transparent ;
    
}
.navSection a:hover {
    
    border-bottom: 2px solid #cd2c2c;
    transition: all 0.2s linear;
}
.onePageFull {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.onePageFull img {
    margin-top: 10px;
    
}
.activeLink {
    color: #cd2c2c;
}
.noActiveLink {
    color: bisque;
}