.mainPage{
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    flex-wrap: wrap;
}
.mainPageOne{
    display: flex;
    flex-direction: row;
    justify-content: center;
    text-align: center;
    flex-wrap: wrap;    
}
.mainPage h1{
    font-family: Arial, Helvetica, sans-serif;
    color: #cd2c2c;
}
.buttonPages {
    display: flex;
    flex-direction: row;
    justify-content: center;
    text-align: center;
    flex-wrap: wrap; 
    margin: 10px 0;
}
.buttonPages button {
    width: 200px;
    color:bisque;
    background-color: #36322f;
    padding: 2px 5px;
    margin: 5px;
    border: none;
}
.buttonPages button:hover {
    cursor: pointer;
    color: #cd2c2c;
}
article {
    width: 200px;
    padding: 5px;
    margin: 5px;
    box-shadow: 2px 2px 5px rgb(176, 175, 175);
}
article:hover {
    box-shadow: 2px 2px 5px rgb(136, 60, 60);
    scale: 1.01;
    transition: all .2s linear;
}
article img {
    width: 100%;
    height: auto;
}
article h3 {
    font-family: sans-serif;
    color: #36322f;
}
article p {
    margin-bottom: 3px;
    font-style: italic;
    margin:20px 0;
}
article a {
    background-color: #36322f;
    color: bisque;
    padding: 2px 5px;
    text-decoration: none;
    border-radius: 5px;
    cursor: pointer;
}
    
article a:hover {
    color: #cd2c2c;
}